import React, { lazy, Suspense, useEffect } from 'react';
import { StandardPageLayout } from 'shared/layouts';
import { usePayments } from './hooks';
import { Helmet } from 'react-helmet';
import clsx from 'clsx';
import { CustomMuiTabs, ErrorBoundary, Loader } from 'shared/components';
import { Link, Route, Switch } from 'react-router-dom';
import { PaymentContextProvider } from './context/PaymentContext';
import { useTranslation } from 'react-i18next';

function a11yProps(index: number) {
	return {
		id: `payments-tabpanel-${index}`,
		'aria-controls': `payments-tab-${index}`
	};
}

const BillingPage = lazy(() => import('./NestedPages/billing'));
const InvoicesPage = lazy(() => import('./NestedPages/invoices'));

function PaymentsInner() {
	const { t } = useTranslation();
	const {
		primeTab,
		setPrimeTab,
		pageName,
		url,
		path,
		unreadCasesQuantity,
		isHidden,
		handleChangePrime
	} = usePayments();

	const isRequestPage = location.pathname.includes('invoices');

	const pathnameList = ['billing'];

	const notBottomExtended = pathnameList.find((path) => location.pathname.includes(path));

	useEffect(() => {
		if (isRequestPage) {
			setPrimeTab(1);
		}
	}, [location, isRequestPage]);

	return (
		<StandardPageLayout>
			<div>
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>{`KENNEKT | ${pageName}`}</title>
					</Helmet>
				)}
				<div className="mb-4">
					<h1 className="text-2xl font-medium dark:text-white">{t('billing_invoices')}</h1>
					<p className="text-sm text-kgrey">{t('choose_right_plan_for_you')}</p>
				</div>
				<div
					className={clsx('prime-tabs', {
						'prime-tabs_no-bottom-extend': notBottomExtended
					})}
				>
					<CustomMuiTabs.Tabs
						roundedTop={!notBottomExtended}
						value={primeTab}
						// @ts-ignore
						onChange={handleChangePrime}
						aria-label="simple tabs"
					>
						<CustomMuiTabs.Tab
							label={t('billing')}
							component={Link}
							to={`${url}/billing`}
							{...a11yProps(0)}
						/>
						<CustomMuiTabs.Tab
							label={t('invoices')}
							component={Link}
							to={`${url}/invoices`}
							{...a11yProps(1)}
						/>
					</CustomMuiTabs.Tabs>
				</div>
				<div className="relative flex max-w-full flex-grow flex-col">
					<div className="relative flex-grow">
						<Suspense fallback={<Loader tiled />}>
							<Switch>
								<Route exact path={`${path}/billing`} component={BillingPage} />
								<Route exact path={`${path}/invoices`} component={InvoicesPage} />
							</Switch>
						</Suspense>
					</div>
				</div>
			</div>
		</StandardPageLayout>
	);
}

export function Payments() {
	return (
		<ErrorBoundary>
			<PaymentContextProvider>
				<PaymentsInner />
			</PaymentContextProvider>
		</ErrorBoundary>
	);
}
