import React, { createContext, useReducer, useContext, useCallback } from 'react';

export type PaymentFrequencyType = 'month' | 'year';

type StateType = {
	searchQuery: string | null;
	paymentFrequency: PaymentFrequencyType;
};

type ContextType = {
	setPaymentFrequency: (frequency: PaymentFrequencyType) => void;
} & StateType;

// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
export const PaymentContext = createContext<ContextType>(null!);

type setSearchQuery = {
	type: 'SET_SEARCH_QUERY';
	payload: string | null;
};

type setPaymentFrequency = {
	type: 'SET_PAYMENT_FREQUENCY';
	payload: PaymentFrequencyType;
};

type Actions = setSearchQuery | setPaymentFrequency;

const reducer = (state: StateType, action: Actions): StateType => {
	switch (action.type) {
		case 'SET_SEARCH_QUERY': {
			return {
				...state,
				searchQuery: action.payload
			};
		}
		case 'SET_PAYMENT_FREQUENCY': {
			return {
				...state,
				paymentFrequency: action.payload
			};
		}
		default:
			return state;
	}
};

export function PaymentContextProvider({
	children
}: React.PropsWithChildren<Record<string, unknown>>) {
	const [state, dispatch] = useReducer(reducer, {
		searchQuery: null,
		paymentFrequency: 'year'
	});

	const { searchQuery, paymentFrequency } = state;

	const setSearchQuery = useCallback((query: string | null) => {
		dispatch({ type: 'SET_SEARCH_QUERY', payload: query });
	}, []);
	const setPaymentFrequency = useCallback((paymentFrequency: PaymentFrequencyType) => {
		dispatch({ type: 'SET_PAYMENT_FREQUENCY', payload: paymentFrequency });
	}, []);

	// eslint-disable-next-line react/jsx-no-constructed-context-values
	const contextValues = {
		searchQuery,
		setSearchQuery,
		paymentFrequency,
		setPaymentFrequency
	} as ContextType;

	return <PaymentContext.Provider value={contextValues}>{children}</PaymentContext.Provider>;
}

export const usePaymentContext = () => {
	const newsPaymentContext = useContext(PaymentContext);

	if (!newsPaymentContext)
		throw new Error('useNewsContext must be used within the NewsContextProvider');
	return newsPaymentContext;
};
